      description:
        "<p>WORKSHOP OVERVIEW</p> <p>You want to design end-to-end user experiences with user research and fast UI prototyping? Learn the right methods and process from an expert with 10+ years real-word design innovation experience.</p> <p>WHO SHOULD ATTEND</p> <p>In previous workshops the graduates had background in online marketing, project management, product management, software development or graphic design. Some of the companies I was running workshops with include companies like: BMW, Roche, Postbank, Munich Re, innogy/eon, ADAC, Viessmann, SinnerSchrader, JvM etc.</p> <p>What they all had in common is they need to:<br/>- Find out what’s really behind UX/UI design<br/>- Learn new customer centric and agile methodologies you can apply in your daily work<br/>- Improve ways to involve customer insights into your product development cycles<br/>- Create more successful products, customers love</p> <p>WHAT IS COVERED</p> <p>Day 1 – Defining the User Experience (UX):<br/>- Conducting initial user research<br/>- Extracting insights from interviews for an early vision<br/>- Defining user segments and map out early functionality<br/>- Creating a user journey<br/>- Communicating our solution with a landing page</p> <p>Day 2 – Designing the User Interface (UI):<br/>- Learning basics of human cognition and psychology<br/>- Inspecting an UI based on 10 rules<br/>- Drafting a design of basic UI layout<br/>- Building paper prototypes or click dummies for user testing<br/>- Learning about prototyping tools, UI animations and history of UI</p> <p>This workshop is proven in many workshops over the last 8 years and regularly updated to match modern standards. Previous attendees rated this workshop with an average score of 9 out of 10.</p> <p>YOUR COACH</p> <p>Thomas Gläser is a passionate product leader who has more than 10 years experience in building successful digital products. He’s working at the intersection of design, business and technology for well-known companies like BMW, Mozilla, Arduino and local startups. Thomas is particularly passionate about helping teams to build better products and executives, to build the right system to support that.</p> <p>He co-founded popular events like push.conference and the UX & Product Meetup in Munich, which unite the local community with global experts in the field. Thomas loves to share his wide-ranging knowledge as a founder, consultant and manager in articles, keynotes and workshops for many years already. In his opinion technology is just a tool to make people happy.</p> <p>YOUR EXTRA BENEFIT</p> <p>All participants will receive a personal 45 minute follow-up call after the training. In this way you can later take up all the questions that have arisen for you in your own application.<br/>YOUR COACH</p> <p>Questions? Get in touch with the workshop host directly: [masked]</p> <p>BOOKING</p> <p>via meetup.com or on Eventbrite (more payment options besides PayPal and team discounts possible)",
